@font-face {
  font-family: 'DINNextLTPro-Heavy';
  src: url('../fonts/DINNextLTPro-Heavy.otf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'DINNextLTPro-Medium';
  src: url('../fonts/DINNextLTPro-Medium.otf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'Nulshock-Book';
  src: url('../fonts/Nulshock-Book.otf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'OpenSans-Regular';
  src: url('../fonts/OpenSans-Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}

